:root {
    --color-primary: #191d2b;
    --color-secondary: rgb(234, 155, 51);
    --color-grey-4: #454e60
}

.projects {
    height: 110vh;
    color: lightgray;
    background-color: var(--color-primary);
}

.title {
    font-size: 3vw;
}

.project-left-hidden {
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-20%);
    transition: .7s all;
}

.project-right-hidden {
    opacity: 0;
    filter: blur(5px);
    transform: translateX(20%);
    transition: .7s all;
}

.project-show {
    opacity: 1;
    transition: .7s all;
    transform: translateX(0%);
}

.project-info {
    width: 60%;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
}

.project-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-photo {
    height: 300px;
    width: 500px;;    
    border-radius: 10px;        
    box-shadow: 0 3px 15px rgba(0,0,0,.3);

}

@media only screen and (max-width: 768px) {
    .project-content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .project-photo {
        height: 200px;
        width: 350px;;    
        border-radius: 10px;        
        box-shadow: 0 3px 15px rgba(0,0,0,.3);
    
    }

    .projects {
        height: 140vh;
        color: lightgray;
        background-color: var(--color-primary);
    }
}